










import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import moment from "moment";
@Component({
  components: {}
})
export default class ArticleLink extends Vue {
  @Prop() article!: any;
  getFullTime() {
    return moment(this.article.stime).format("YYYY-MM-DD");
  }
}



