























import Vue from "vue";
import Component from "vue-class-component";
import ArticleLink from "./ArticleLink.vue";
import { article } from "../../server/article";

import { me } from "../../core/event";
@Component({
  components: { ArticleLink }
})
export default class First extends Vue {
  list: any = [];
  async mounted() {
    await article.getArticleList();
    me.Event.emit("article_list");
    let list = article.rawlist;
    list.slice(0, 10);
    this.list = list;
  }
}



